<template>
  <div class="maindiv">
  <Row>


    <Col :xs="{span:20,offset:1} " :sm="{span:8,offset:8}" :md="{span:8,offset:8}" :lg="{span:8,offset:8}" style="width: 342px" >
      <Card class="main">
        <div  slot="title" style="height: auto">
          <h1>物流</h1>
        </div>
        <Divider class="divider">货物id：{{$route.params.id}}</Divider>

        <div>
          <Steps :current="2" size="small">
            <Step title="起点"></Step>
            <Step title="中转站1"></Step>
            <Step title="中转站2"></Step>
            <Step title="终点"></Step>
          </Steps>
            <p>货物此时在<SPAN>中转站2</SPAN></p>

        </div>
        <p-footer></p-footer>
      </Card>

    </Col>



  </Row>
  </div>
</template>

<script>
  import Footer from '@/components/footer'
  export default {
    name: "Home",
    components: {
      'p-footer': Footer
    },
    data () {
      return {

        current: 1
      }
    },
    methods: {

    }
  }
</script>

<style>
  .demo-drawer-profile{
    font-size: 14px;
  }
  .demo-drawer-profile .ivu-col{
    margin-bottom: 12px;
  }
  .main{height: 411px;overflow: hidden}
</style>



